.App{margin:0;padding:0;height:100vh;box-sizing:border-box;letter-spacing:0.1rem;padding:0 6rem}.Navigation__feature{margin:0 auto}

.About{width:80%;margin:0 auto;margin-top:1rem;display:flex;justify-content:space-between}.About__img>img{width:28rem}.About__content{padding-left:10rem;color:#680606}.About__content>h1{font-size:3rem;font-weight:700;line-height:1.25}.About__content>p{line-height:1.5;font-size:1.3rem;font-weight:400}.About__content>img{margin-top:2rem;width:35%}.About__content-list{position:relative;top:2rem;display:flex;justify-content:space-around;list-style:none;text-align:center;text-transform:capitalize;padding-left:0}.About__content-list--link{position:relative;top:0.5rem;display:block;text-decoration:none;font-size:1.2rem;color:#680606;transition:all 0.4s ease-in-out 0s}.About__content-list--link:hover{color:#ee1919;position:relative;top:0rem;transition:all 0.3s ease-in-out 0s}

.contact{text-align:center;width:50%;height:32rem;margin:5rem auto;border:1rem solid #6d6f71}.contact h1{font-weight:400;font-size:3rem;color:#680606}.contact__form--input{width:70%;margin:1.5rem 0;padding-left:1rem;background-color:#f1f1f1;line-height:200%;border:0.125rem solid #fff;font-family:"Courgette", cursive;font-size:1.2rem;font-weight:400;line-height:3rem}.contact__form--textArea{width:70%;margin-bottom:1.5rem;padding-left:1rem;font-size:1.3rem;background-color:#f1f1f1;line-height:200%;border:0.125rem solid #fff;font-family:"Courgette", cursive;font-size:1.2rem;font-weight:400;line-height:3rem}.contact__form--button{width:73%;height:2.5rem;background-color:#471111;font-family:"Courgette", cursive;color:#fff;font-size:1.2rem;border-radius:0.5rem;margin-bottom:1.5rem;cursor:pointer}.contact__form--button:hover{background-color:#680606}

.Introduction{position:relative;color:#680606;margin-top:5rem;display:flex;flex-direction:column;text-align:center}.Introduction__information--img{width:auto}.Introduction__information--img>img{width:25%;border-radius:50%;justify-content:center;transition:all 0.4s ease 0s}.Introduction__information--img>img:hover{cursor:pointer;transform:scale(0.8) rotate(-16deg)}.Introduction__information--skill{letter-spacing:1ps;font-size:1.2rem}.Introduction__information--title{font-size:3rem}.Introduction__information>a{text-decoration:none}.Introduction__information--btn{position:relative;top:1rem;text-decoration:none;margin:0 auto;background-color:#ec7474;height:3rem;width:11rem;border-radius:1rem;transition:all 0.3s ease-in-out 0s;display:flex;justify-content:space-around}.Introduction__information--btn>p{display:block;width:5rem;display:inline;text-decoration:none;font-family:"Courgette", cursive}.Introduction__information--btn>svg{display:block;width:2rem;height:2.5rem;text-align:center;font-weight:300}.Introduction__information--btn:hover{position:relative;top:0rem;transition:all 0.4s;cursor:pointer}

.Navigation{position:relative;display:flex;justify-content:space-between}.Navigation__name{font-weight:400;cursor:pointer;font-weight:400;font-size:1.6rem;color:#680606}.Navigation__list{display:flex;justify-content:space-around;padding-top:0.5rem;list-style:none;text-align:center;text-transform:capitalize}.Navigation__list--title{padding-right:2.5rem}.Navigation__list--link{display:block;text-decoration:none;font-size:1.2rem;color:#680606}.Navigation__list--link:hover{color:#ee1919}

.layout{position:relative;padding:0.75rem}.layout__hidenlay{width:23.75rem;height:16rem;z-index:1;transition:all 0.4s ease-in-out 0s}.layout__hidenlay>h1{position:absolute;top:5rem;left:3rem;color:aliceblue}.layout__hidenlay>img{width:23.75rem;height:16rem;border-radius:1rem}.layout__overlay{position:absolute;top:0.75rem;opacity:0;display:block;width:23.75rem;height:16rem;border-radius:1rem;transition:all 0.4s ease-in-out 0s;text-align:center}.layout__overlay:hover{display:block;opacity:1;transition:all 0.4s ease-in-out 0s}.layout__overlay>h2{padding-top:3rem;color:azure}.layout__overlay-link{display:block;margin:0 auto;width:10rem;height:2.5rem;text-decoration:none;color:aliceblue;border:0.15rem solid #7846b1;border-radius:5rem;margin-top:2rem;transition:all 0.3s ease-in-out 0s}.layout__overlay-link:hover{display:block;margin-top:1.5rem;transition:all 0.3s ease-in-out 0s}.layout__overlay-link--title{padding-top:0.5rem;display:inline-block;height:1.5rem}.layout__overlay-link--icon>svg{width:2rem;height:0.6rem}.Project{display:block;width:100%}.Project__content{text-align:center;color:#680606;width:70%;margin:0 auto;margin-bottom:2rem}.Project__content>h1{font-weight:500}.Project__content>p{font-size:larger}.Project__content>a{text-decoration:none}.Project__content--btn{position:relative;top:1rem;text-decoration:none;margin:0 auto;background-color:#ec7474;height:3rem;width:11rem;border-radius:1rem;transition:all 0.3s ease-in-out 0s;display:flex;justify-content:space-around}.Project__content--btn>p{display:block;width:5rem;display:inline;font-family:"Courgette", cursive;text-decoration:none}.Project__content--btn>svg{display:block;width:2rem;height:2.5rem;text-align:center;font-weight:300}.Project__content--btn:hover{position:relative;top:0rem;transition:all 0.4s;cursor:pointer}.Project__box{text-align:center;display:flex;flex-wrap:wrap;padding:0 3rem}.invitation{margin-top:5rem}

